\n    box-sizing: border-box;\r\n    position: relative;\r\n    padding: 30px 0px;\r\n    background-image: linear-gradient(to bottom,$lightOrangeColor,$mainColor);\r\n    border-radius: 150px 150px 50px 50px;\r\n    p{\r\n      color: $yellowColor;\r\n      margin: 0;\r\n      text-align: center;\r\n      font-size: 40px;\r\n    }\r\n    .giftBagImg{\r\n      display: block;\r\n      width: 100%;\r\n      margin: 40px auto;\r\n    }\r\n    .giftbag-box-but{\r\n      display: block;\r\n      width: 70%;\r\n      height: 85px;\r\n      margin: auto;\r\n      border: none;\r\n      background-color: $yellowColor;\r\n      border-radius: 100px;\r\n      color: $redColor;\r\n      font-size: 40px;\r\n    }\r\n    .close{\r\n      color: $whiteColor;\r\n      position: absolute;\r\n      left: 42%;\r\n      bottom: -150px;\r\n      font-size: 80px;\r\n    }\r\n  }\r\n}\r\n.index{\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: $lightGrey;\r\n  color: #000000;\r\n  position: relative;\r\n  .slide{\r\n    height: 100%;\r\n    overflow: hidden;\r\n    overflow-x: hidden;\r\n    overflow-y: scroll;\r\n  }\r\n  .head{\r\n    height: 95px;\r\n    line-height: 98px;\r\n    text-align: center;\r\n    background-color: $whiteColor;\r\n    color: $grayBlueColor;\r\n    font-weight: 700;\r\n    font-size: 38px;\r\n  }\r\n  .answersheet-list{\r\n    width: 100%;\r\n    padding-bottom: 97px;\r\n    li{\r\n      width: 100%;\r\n      background-color: $whiteColor;\r\n      margin: 15px 0;\r\n      padding: 15px;\r\n      box-sizing: border-box;\r\n      p{\r\n        box-sizing: border-box;\r\n        padding: 20px 15px 0px 15px;\r\n        margin: 0;\r\n        text-align:left;\r\n        text-align: justify;\r\n        font-size: 35px;\r\n      }\r\n      .radio{\r\n        width: 100%;\r\n        box-sizing: border-box;\r\n        padding: 0 50px;\r\n        .radio-group{\r\n          margin: 35px 0px;\r\n        }\r\n        .van-radio {\r\n          align-items: stretch !important;\r\n      }\r\n        .van-icon{\r\n          width: 35px !important;\r\n          height: 35px !important;\r\n          line-height: 35px !important;\r\n          font-size: 30px !important;\r\n        }\r\n        .van-radio__icon{\r\n          height: 35px;\r\n          padding: 1vw 1vw 0vw 0vw;\r\n        }\r\n        .van-radio--horizontal{\r\n          margin-right: 0px !important;\r\n          min-height: 45px;\r\n          .van-radio__label {\r\n            line-height: 50px;\r\n            text-align:left;\r\n            text-align: justify;\r\n        }\r\n        }\r\n      }\r\n    }\r\n  }\r\n  .submit{\r\n    width: 100%;\r\n    height: 95px;\r\n    background-color: $grayBlueColor;\r\n    border: none;\r\n    position: absolute;\r\n    bottom: 0;\r\n    color: $whiteColor;\r\n    font-size: 38px;\r\n  }\r\n  \r\n}\r\n\r\n/* 核销码 */\r\n.qrcode{\r\n  h3{\r\n    text-align: center;\r\n    color: $blueColor;\r\n    margin-top: 30%;\r\n  }\r\n  .code{\r\n    width: 100%;\r\n    #qrcode{\r\n      display: flex !important;\r\n      justify-content: center !important;\r\n    }\r\n  }\r\n  p{\r\n    text-align: center;\r\n  }\r\n}","\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"./styles/index.scss\";\n.child-view {\n    will-change: transform;\n    transition: all 0.4s cubic-bezier(0.55,0,0.1,1);\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    backface-visibility: hidden;\n    perspective: 1000;\n    box-sizing: border-box\n  }\n.slide-left-enter, .slide-right-leave-to {\n  opacity: 0;\n  transform: translateX(100%)\n}\n.slide-left-leave-to, .slide-right-enter {\n  opacity: 0;\n  transform: translateX(-100%)\n}\n\n.slide-left-enter-active, .slide-left-leave-active, .slide-right-enter-active, .slide-right-leave-active {\n  transition: 0.3s;\n  position: absolute;\n  top:0;\n}\nhtml,\nbody {\n  margin: 0;\n  padding: 0;\n  height: 100%;\n  width: 100%;\n  overflow-x: hidden;\n  font-family: Helvetica